<script>
    import { onMount } from 'svelte';
	// import {resizeImg} from 'resize-img'
	import loadImage from "blueimp-load-image/js/load-image.js"
    import  "blueimp-load-image/js/load-image-scale.js"

    export let display = 'none';

    export let src ="../assets/user.svg";
    export let name;
    export let email;
    let form;
    let files;

    $: if (files) {
        // Переменная `files` будет типа `FileList`, а не массивом:
        // https://developer.mozilla.org/ru/docs/Web/API/FileList
        console.log(files);
    }

    onMount(async () => {
        // window.parent.document.body.append(document.getElementsByClassName('form-signin')[0]);
        window.parent.document.body.append(form);

        let item  = JSON.parse(localStorage.getItem('kolmi_abonent'));
        name = item.name;
        email = item.email;
        src = item.src;

    });


    function OnClickUpload(){
        let event =  new MouseEvent('click', {
        bubbles: true,
        cancelable: true,
        view: window,
        });
        window.parent.document.getElementById('avatar').dispatchEvent(event);
        window.parent.document.getElementById('avatar').addEventListener("change", OnChangeFile);
    }


    async function OnChangeFile(e){
        console.log();
        try {

        let data = await loadImage(
            e.target.files[0],
            function (img, data) {
                if(img.type === "error") {
                    console.error("Error loading image ");
                } else {
                    src =  img.toDataURL();
                    window.parent.document.getElementById('avatar_img').src = src;
                    window.parent.document.getElementById('avatar_input').value = src;
                    // document.querySelectorAll('input[type=file]').attributes.changed = true;
                }
            },
            {
                orientation:true,
                maxWidth: 300,
                maxHeight: 300,
                minWidth: 100,
                minHeight: 50,
                canvas: true
            }
        );
        }catch(ex){

        }
    }

</script>


<form  class="form-signin"  style='display:{display}; position:absolute;top:0' bind:this={form}>
    <style>
        input{
            border-width:0;
        }

        .form-signin {
            background-color: white;
            max-width: 90%;
            /* max-height: 90%;            */
            border: 3px solid grey;
            padding: 10px;
            margin: 0 auto;
        }
        .form-signin .form-signin-heading,
        .form-signin .form-control {
            position: relative;
            height: auto;
            -webkit-box-sizing: border-box;
            -moz-box-sizing: border-box;
            box-sizing: border-box;
            padding: 10px;
            font-size: 16px;
        }
        .form-signin .form-control:focus {
            z-index: 2;
        }
        .form-signin input[type="email"] {
            margin-bottom: -1px;
            border-bottom-right-radius: 0;
            border-bottom-left-radius: 0;
        }
    
    </style>
    <div class="row" style="border-style: groove; border-radius:2px;border-color: #cbced2;padding: 10px">
        <h2 class="form-signin-heading">Как Вас представить?</h2>
        <div>
            <h6>Upload your photo...</h6>
            <img {src} class="avatar" id="avatar_img"
            width="200px" alt="avatar" on:click={OnClickUpload}>
      
            <input  class="file-upload" on:change="{OnChangeFile}"
                accept="image/png, image/jpeg"
                bind:files
                id="avatar"
                name="avatar"
                type="file"  
                style="display: none"/>
        </div>
        <div>
            <input type="text" id="name" class="form-control" placeholder="input name" required bind:value = {name}>
            <input type="email" id="email" class="form-control" placeholder="input email" required bind:value = {email}>
            <div for="send_form">&nbsp;</div>
            <button id="send_form" class="btn btn-primary"  on:click|preventDefault|stopPropagation>Сохранить и закрыть</button>
        </div>
    </div>
</form> 

